import { Button } from '@material-ui/core';
import { useState } from 'react';
import data from './data';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const App = () => {
  const [disable, setDisable] = useState(false);
  const [render, setRender] = useState(false);
  const [val, setVal] = useState();
  const makerequest = () => {
    setTimeout(() => {
      setVal(data);
      setRender(true);
      setDisable(false);
    }, 1000);
  };
  const handleSubmit = () => {
    setDisable(true);
    makerequest();
  };
  return (
    <div>
      <Button
        disabled={disable ? true : false}
        onClick={handleSubmit}
        variant='outlined'
      >
        Get Data
      </Button>
      {render === true ? (
        <TableContainer component={Paper}>
          <Table aria-label='simple table'>
            <TableHead>
              <TableRow>
                <TableCell>Amount</TableCell>
                <TableCell align='right'>CD</TableCell>
                <TableCell align='right'>Description</TableCell>
                <TableCell align='right'>PCTime</TableCell>
                <TableCell align='right'>Reference</TableCell>
                <TableCell align='right'>TransactionDate</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {console.log()}
              {val[0].transactions.map(el => (
                <TableRow key={el.Reference}>
                  <TableCell component='th' scope='row'>
                    {el.Amount}
                  </TableCell>
                  <TableCell align='right'>{el.CD}</TableCell>
                  <TableCell align='right'>{el.Description}</TableCell>
                  <TableCell align='right'>{el.PCTime}</TableCell>
                  <TableCell align='right'>{el.Reference}</TableCell>
                  <TableCell align='right'>{el.TransactionDate}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      ) : (
        ''
      )}
    </div>
  );
};

export default App;
